.first-screen {
    padding-top: 31px;
    padding-bottom: 90px;
}

.slider-info {
    @extend .container-xxl;
   

.swiper-slide {
       display: flex;
       align-items: center;
       min-height: 700px;
       background-size: cover;
       border-radius: 20px;
       padding: 0 72px 90px;
   }


.swiper-pagination {
    bottom: 35px;
}



.arrow-container {
    width: 100%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    max-width: 1570px;
}

.swiper-button-next, 
.swiper-button-prev {
    color: #fff;
    width: 72px;
    height: 48px;
    &:hover {
        color: $color-secondary ;
    }
  }
}

.swiper-pagination-bullet {
    background: transparent;
    opacity: 1;
    border: 2px solid;
    &.swiper-pagination-bullet-active {
        background-color: #fff;
    }
}


.banner {    
    margin-bottom: 70px;
    
    .banner-title {
        font-size: 54px;
        font-weight: 700;
        line-height: 1.26;
        text-transform: uppercase;
        margin-bottom: 9px;
        max-width: 480px;
}
     .banner-description {
        font-weight: 300;
        font-size: 21px;
        line-height: 1.36;
        margin-bottom: 37px;
        max-width: 340px;
}

}





